.product__item {
  width: calc(100% / 3);
  padding: 20px;
}

.product__wrap {
  border-radius: 4px;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
}

.product__img {
  position: relative;
  height: 270px;
}

.product__img:hover .product__img-layer {
  opacity: 0.4;
}

.product__img:hover .product__btn {
  opacity: 1;
}

.product__img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
}

.product__img-layer {
  width: 100%;
  height: 100%;
  position: absolute;
  background: #000;
  top: 0;
  left: 0;
  transition: all 0.5s;
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
  opacity: 0;
}

.product__item-wrap {
  padding: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 16px;
  border-radius: 4px;
}

.product__item-wrap span {
  color: var(--primary);
}

.product__btn {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  transition: all 0.5s;
}

.product__icon {
  background-color: var(--primary);
  color: var(--text-white);
  /* padding: 10px; */
  width: 35px;
  height: 35px;
  font-size: 28px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
}

