.about {
  padding: 80px 0;
}

.about__container {
  display: flex;
  max-height: 500px;
}

.about__left,
.about__right {
  width: 50%;
}

.about__left img {
  height: 100%;
  width: 90%;
  object-fit: cover;
  border-radius: 4px;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  transition: all 0.5s;
  animation: jumpInCenter 1s;
}

.about__left img:hover {
  transform: translateY(-20px);
}

.about__title {
  animation: moveUp 1s;
}

.about__title h3 {
  font-size: 38px;
  color: var(--text-home-title);
}

.about__title .underline {
  margin: 0;
  border-radius: 2px;
  width: 90px;
}

.about__right p {
  font-size: 20px;
  color: var(--text-home-sub);
  margin-top: 20px;
  line-height: 28px;
  animation: moveUp 1s;
}
