.nav {
    background-color: var(--home-bg-3);
  }
  
  .nav__container {
    padding: 20px;
    color: var(--text-home-title);
    font-size: 30px;
  }
  
  .nav__container a {
    color: var(--primary);
    text-decoration: none;
    margin-right: 8px;
    transition: all 0.5s;
  }
  
  .nav__container a:hover {
    color: var(--text-home-title);
  }