.products {
  padding: 80px 0;
}

.products__container {
  display: flex;
}

.products__navbar {
  width: 20%;
  /* box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px; */
}

.form__search {
  padding: 10px 20px;
  width: 50%;
  border: none;
  border-radius: 4px;
  background-color: var(--home-bg-2);
  color: var(--text-home-title);
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
}

.form__title {
  font-size: 20px;
  color: var(--text-home-title);
  margin-top: 20px;
}

.category__list {
  list-style: none;
}

.category__item {
  margin: 10px;
  color: var(--text-home-sub);
  position: relative;
}

.category__item span {
  position: relative;
  padding: 5px 0;
  display: inline-block;
  cursor: pointer;
}

.category__item span:hover::after {
  width: 100%;
}

.category__item span::after {
  content: "";
  width: 0;
  height: 2px;
  background-color: var(--primary);
  position: absolute;
  bottom: -4px;
  left: 0;
  transition: all 0.5s;
}

.category__item--active {
  color: var(--primary);
}

.category__item--active span::after {
  width: 100%;
}

.company {
  border: none;
  padding: 10px;
  background-color: var(--home-bg-2);
  margin-top: 10px;
  border-radius: 4px;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  color: var(--text-home-title);
  cursor: pointer;
}

.company option {
  padding: 10px;
}

.colors__list {
  list-style: none;
  display: flex;
}

.colors__item {
  margin: 5px;
  display: block;
  opacity: 0.5;
}

.colors__item span {
  width: 15px;
  height: 15px;
  /* background-color: rgb(218, 218, 250); */
  border-radius: 50%;
  font-size: 12px;
  display: block;
  align-items: center;
  justify-content: center;
  text-align: center;
  line-height: 20px;
  color: var(--text-home-sub);
  cursor: pointer;
  line-height: 15px;
  /* opacity: 0; */
}

.colors__item span::first-letter {
  text-transform: uppercase;
}

.colors__item span svg {
  opacity: 0;
  height: 15px;
}

.colors__item--active,
.colors__item--active span svg {
  opacity: 1;
}

.colors__item--active span {
  color: var(--text-white);
}

.colors__item--all {
  opacity: 1;
}

.colors__item--all span {
  border-bottom: 2px solid var(--primary);
  border-radius: unset;
  color: var(--primary);
}

.form__current-price {
  padding: 10px 10px 0;
  color: var(--text-header-color);
  display: block;
}

.form__price {
  height: 5px;
  width: 50%;
  padding: 10px;
}

.form__price-layer {
  height: 5px;
  width: 100%;
  background-color: var(--form-price-bg);
  position: relative;
  border-radius: 2px;
}

.form__progress-wrap {
  height: 5px;
  width: 80%;
  background-color: var(--primary);
  position: absolute;
  left: 0;
  bottom: 0;
  border-radius: 2px;
}

.form__price-progress {
  height: 5px;
  width: 100%;
  position: relative;
}

.form__progress-circle {
  height: 15px;
  width: 15px;
  background-color: var(--primary);
  border-radius: 50%;
  position: absolute;
  left: 96%;
  bottom: 50%;
  transform: translateY(50%);
  cursor: pointer;
}

.form__shipping {
  display: flex;
  align-items: center;
  margin-top: 20px;
  /* font-size: 16px; */
  color: var(--text-header-color);
  /* justify-content: ; */
}

.ship {
  margin-left: 10px;
}

.form-btn {
  padding: 10px;
  margin-top: 20px;
}

