/* product-lisy */

.products__wrap {
  width: 80%;
}

.products__features {
  padding: 0 20px;
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.products__style {
  display: flex;
  align-items: center;
}

.products__style span {
  font-size: 18px;
  color: var(--text-home-title);
}

.products__features-line {
  flex: 1;
  padding: 0 30px;
  position: relative;
  margin: 0 20px;
}

.products__features-line div {
  height: 1px;
  width: 100%;
  padding: 0 40px;
  background-color: var(--home-bg-3);
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
}

.products__icon-wrap {
  display: flex;
}

.products__icon {
  margin-right: 10px;
  font-size: 20px;
  cursor: pointer;
}

.products__icon--active {
  background-color: black;
  color: white;
}

.products__icon svg {
  /* background-color: #333;
  color: white; */
  display: block;
}

.products__icon i {
  padding: 4px;
  background-color: var(--home-bg-1);
  color: var(--text-home-title);
  border-radius: 4px;
  font-size: 20px;
  cursor: pointer;
}

.products__icon .products__icon--active {
  background-color: var(--home-bg-4);
  color: var(--text-white);
}

.products__list {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  max-height: 1000px;
  overflow-y: scroll;
  overflow: -moz-scrollbars-vertical;
}

.products__list::-webkit-scrollbar {
  display: none;
}

.products__list {
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
}

.products__wrap .product__item {
  max-height: 300px;
}

.products__wrap .product__wrap {
  height: 100%;
}

.products__wrap .product__img {
  max-height: 70%;
}

.products__wrap .product__item-wrap {
  height: 30%;
}
