.header__wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 0;
}

.header__logo,
.header__features {
  width: 200px;
}

.header__logo img {
  width: 180px;
}

.header__nav-list {
  list-style: none;
  display: flex;
}

.header__nav-item {
  margin: 0 10px;
}

.header__nav-item a {
  text-decoration: none;
  padding: 0px 20px;
  color: var(--text-header-color);
  font-size: 20px;
  position: relative;
}

.header__nav-item--active a {
  color: var(--primary);
}

.header__nav-item a span {
  padding: 10px 0;
  position: relative;
}

.header__nav-item a span::after {
  content: "";
  height: 2px;
  width: 0px;
  position: absolute;
  bottom: 0;
  left: 0;
  background-color: var(--primary);
  transition: all 0.5s;
}

.header__nav-item a:hover span::after {
  width: 100%;
}

.header__features {
  display: flex;
  justify-content: center;
}

.header__feature {
  color: var(--text-home-title);
  font-size: 28px;
  margin: 0 10px;
  display: flex;
  position: relative;
}

.header__feature p {
  position: absolute;
  background-color: var(--primary);
  color: var(--text-white);
  right: -8px;
  top: -10px;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  font-size: 16px;
  text-align: center;
  line-height: 20px;
}

.header__feature span {
  margin-right: 8px;
}
