.design {
  padding: 20px 0 80px;
}

.design__container {
  display: flex;
  position: relative;
}

.design__left {
  padding: 120px 10px;
  width: 50%;
}

.design__title {
  font-size: 50px;
  max-width: 70%;
  color: var(--text-home-title);
}

.design__left p {
  font-size: 20px;
  color: var(--text-home-sub);
  margin: 30px 0;
  line-height: 28px;
  max-width: 90%;
}

.design__right {
  position: relative;
  width: 50%;
  /* background-color: var(--primary); */
  /* padding: 80px 0; */
  max-height: 550px;
}

.design__layer {
  /* width: 100%;
      height: 80%; */
}

.design__right img {
}

.design__right img:first-child {
  height: 30%;
  bottom: 0;
  left: 0;
  z-index: 3;
  border-radius: 4px;
  position: absolute;
}

.design__right img:last-child {
  height: 100%;
  width: 100%;
  object-fit: contain;
  border-radius: 4px;
  z-index: 2;
  position: relative;
}

.design__right:after {
  content: "";
  position: absolute;
  width: 50%;
  height: 50%;
  background-color: var(--primary);
  left: 10%;
  bottom: 30%;
  border-radius: 4px;
  opacity: 0.5;
}

/* end design */

/* Start features */

.features {
  padding: 80px 0;
  background-color: var(--home-bg-2);
}

.features__title {
  text-align: center;
  margin-bottom: 20px;
  color: var(--text-home-title);
  font-size: 32px;
}

.features__title h2 {
  padding-bottom: 10px;
}

.features__wrap {
  display: flex;
  margin-top: 40px;
}

.underline {
  height: 4px;
  width: 10%;
  background-color: var(--primary);
  margin: 0 auto;
}
