* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
}

:root {
  --text-header-color: #324d67;
  --text-home-sub: #617d98;
  --text-home-title: #102a42;
  --home-bg-2: #f1f5f8;
  --home-bg-3: #eaded7;
  --home-bg-4: #333;

  --form-price-bg: #f5d9c9;
  --primary: #ab7a5f;
  --home-bg-1: #fff;
  --text-white: #fff;
  --btn-bg-clear: rgb(247, 119, 119);
}

body {
  background-color: var(--home-bg-1);
}

.btn {
  text-transform: uppercase;
  font-size: 16px;
  font-weight: 500;
  padding: 10px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: all 0.5s;
}

.btn:hover {
  opacity: 0.8;
}

.btn-primary {
  color: var(--text-white);
  background-color: var(--primary);
}

.btn-red {
  color: var(--text-white);
  background-color: var(--btn-bg-clear);
}
